import { useEffect, useState } from 'react'
import './ApiKeyModal.css'
import { qiniuAIAPIService } from '../services/api.service'

type Props = {
  open: boolean
  onClose: () => void
}

function ApiKeyModal({ open, onClose }: Props) {
  const [value, setValue] = useState('')
  const [error, setError] = useState('')

  useEffect(() => {
    if (!open) {
      setValue('')
      setError('')
    }
  }, [open])

  if (!open) return null

  const handleSave = async () => {
    const v = value.trim()
    if (!v) {
      setError('')
      return
    }
    try {
      await qiniuAIAPIService.setApiKey(v)
      window.dispatchEvent(new CustomEvent('api-error', { detail: { message: '', type: 'success', source: 'qiniu' } }))
      onClose()
    } catch (e) {
      setError(e instanceof Error ? e.message : '')
    }
  }

  return (
    <div className="akm-overlay">
      <div className="akm-modal">
        <div className="akm-title"></div>
        <div className="akm-desc"></div>
        <div className="akm-desc"><a href="https://s.qiniu.com/FbMvqa" target="_blank" rel="noopener noreferrer"></a></div>
        <input
          className="akm-input"
          type="password"
          value={value}
          onChange={e => setValue(e.target.value)}
          placeholder=""
        />
        {error && <div className="akm-error">{error}</div>}
        <div className="akm-actions">
          <button className="akm-btn akm-secondary" onClick={onClose}></button>
          <button className="akm-btn" onClick={handleSave}></button>
        </div>
      </div>
    </div>
  )
}

export default ApiKeyModal

